import React from 'react';
import './Heading.scss';

const imgAddress = "data:image/jpeg;base64,"
const HeaderObject = {
    name: 'Hitesh Rajpal', designation: 'Software Engnieer',
    email: 'hiteshrajpal1904@gmail.com', phoneNumber: '8898369341',
    dateOfBirth: '19 April, 1994',
    description: 'I am a guy who look for opprunity and try to find out newer path which would help to make the work easy for everyone'
}
const Heading = () => {
    return (
        <div className="page-container">
        <div className="header-container">
            <img src={imgAddress} className="img" />
            <div className="profile-name">{HeaderObject.name}
                <div className="profile-designation">{HeaderObject.designation}</div>
            </div>
            <div className="profile-details">
                <div>{HeaderObject.email}</div>
                <div>{HeaderObject.phoneNumber}</div>
                <div>{HeaderObject.dateOfBirth}</div>
            </div>

        </div>
        <div className="desc">{HeaderObject.description}</div>
        </div>
    )
}

export default Heading 